import {useField} from "formik"
import { useState } from "react"
import { FiEye, FiEyeOff } from "react-icons/fi"
import { StyledLabel, StyledTextInput,StyledIcons,StyledErrors } from './Styles'



export const TextInput = ({icon, ...props}) => {
    const [Field, meta ] = useField(props)
    const [show, setShow] = useState(false)


    return (
        <div style={{position:"relative"}}>
            <StyledLabel htmlFor={props.name}>{props.label}</StyledLabel>
            {props.type !== "password" && <StyledTextInput invalid={meta.touched && meta.error} {...Field}  {...props}/>}
            {
                props.type === "password" && (<StyledTextInput
                invalid={meta.touched && meta.error}
                    {...Field}
                    {...props}
                    type={show ? "text":"password"}
                />
            )}
            <StyledIcons>{icon}</StyledIcons>
            {
                props.type === "password" && <StyledIcons onClick={()=>{setShow(!show)}} right >
                    {show && <FiEye/>}
                    {!show && <FiEyeOff/>}
                </StyledIcons>
            }

            {meta.touched && meta.error ? (
                <StyledErrors>{meta.error}</StyledErrors>
            ): (
                <StyledErrors style={{visibility:"hidden"}}></StyledErrors>
            )}
        </div>
    )
}