import React from 'react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import axios from "axios"
import { authActions } from '../store'
import { Avatar, Button, colors, ExtractText, StyledFormArea, StyledFormButton, StyledTitle, TextLink, CopyRight, StyledContainer } from '../components/Styles'
import {Formik,Form} from 'formik'
import woman from "../assets/woman.png"
import { TextInput } from '../components/Formlib.js'
import {FiMail,FiLock} from "react-icons/fi"
import * as Yup from "yup"
import { Audio } from 'react-loader-spinner'


const Login = () => {
  const dispath = useDispatch()
  const navigate = useNavigate()
  const [error, setError] = useState()
   
  return (
    <StyledContainer>
       
    <div>
        <StyledFormArea>
            <Avatar image={woman}/>
            <StyledTitle color={colors.theme} size={30}>
                Member login
            </StyledTitle>
            <Formik
                initialValues = {{email:"", password:""}}
                validationSchema={
                    Yup.object({
                        email:Yup.string()
                         .email("invalid email")
                         .required("required"),
                        password:Yup.string()
                         .required("required"),
                })}
                 onSubmit={(values,{setSubmitting})=>{
                    //console.log(values)
                    setTimeout(() => { 
                         const sendRequest=async (type="login") => {
                           const res = await axios.post(`http://localhost:4000/api/user/${type}`, {
                             email:values.email,
                             password:values.password,
                           })
                           .catch(err => {
                            console.log(err.response.data.msg)
                            setError(err.response.data.msg)
                            navigate("/login")

                            })
                            const data = await res.data
                            return data;}
                         sendRequest("login")
                         .then((data)=>{localStorage.setItem("userId", data.user._id)})
                         .then(()=> dispath(authActions.login()))
                         .then(()=> navigate("/blogs"));                      
                     }, 1);
                    
                    
                 }}                
            >                
                {({isSubmitting}) => (
                    <Form>
                        {isSubmitting && ( error && alert(error) ) }
                
                        <TextInput 
                            name="email"
                            placeholder="Email"
                            type="text"
                            ///value={isInput.email}
                            label="Email Address" 
                            //onChange={handleChange}
                            icon={<FiMail />} />
                        <TextInput 
                            name="password"
                            placeholder="Password"
                            type="text"
                            //value={isInput.password}
                            label="Input your password" 
                            ////onChange={handleChange}
                            icon={<FiLock />}/>
                        <Button>
                            {!isSubmitting && (<StyledFormButton type="submit">
                                Login
                            </StyledFormButton>)}
                            {isSubmitting && (
                                <Audio
                                height={80}
                                width={80}
                                radius={9}
                                color={colors.theme}
                                ariaLabel="loading"
                                wrapperStyle
                                wrapperClass
                              />
                            )}
                        </Button>
                    </Form>
                )}
            </Formik>
            <ExtractText>
                New here? <TextLink to="/signup">Signup</TextLink>
            </ExtractText>
        </StyledFormArea>
        <CopyRight>
            All rights reserved &copy;2023
        </CopyRight>
    </div>
    </StyledContainer>
  )
}

export default Login









