import {
  Avatar,
  Box,
  Card,
  CardContent,
  CardHeader,
  CardMedia,
  IconButton,
  Typography,
} from "@mui/material";
import React from "react";
import ModeEditOutlineIcon from "@mui/icons-material/ModeEditOutline";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { useStyles } from "./utils.js";


const Blog = ({ title, description, imageURL, userName, isUser, id }) => {
  const classes = useStyles();
  const navigate = useNavigate();
  const handleEdit = () => {
    navigate(`/myblogs/${id}`);
  };
  const deleteRequest = async () => {
    const res = await axios
      .delete(`http://localhost:4000/api/blog/${id}`)
      .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };
  const handleDelete = () => {
    deleteRequest()
      .then(() => navigate("/"))
      .then(() => navigate("/blogs"));
  };
  return (
    <>
    <div background-color="#605C3C">
      {" "}
      <Card
        sx={{
          width: "60%",
          backgroundColor:"#605C3C",
          margin: "auto",
          mt: 2,
          padding: 2,
          boxShadow: "5px 5px 10px #ccc",
          ":hover": {
            boxShadow: "10px 10px 20px #ccc",
          },
        }}
        >
          {isUser && (
            <Box display="flex">
              <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }}>
                <ModeEditOutlineIcon color="warning" />
              </IconButton>
              <IconButton onClick={handleDelete}>
                <DeleteForeverIcon color="warning" />
              </IconButton>
            </Box>
          )}
          <CardHeader className={classes.root}
            avatar={
              <Avatar
                className={classes.font}
                sx={{ bgcolor: "red" , fontWeight: "bold"}}
                aria-label="recipe"
              >
                {userName ? userName.charAt(0) : ""}
              </Avatar>
            }
            title={
              <Typography gutterBottom variant="h5"  fontWeight="bolder" fontSize="2rem" component="h2">
                 {title}
              </Typography>
           } 
          />
          <CardMedia
            component="img"
            height="300"
            image={imageURL}
            alt="Blog image"
          />
  
          <CardContent>
            <hr />
            <br />
            <Typography
              className={classes.font}
              variant="body2"
              color="#F7F9F9"
              fontWeight="bold"
            >
              <b>{userName}</b> {": "} {description}
            </Typography>
          </CardContent>
        </Card>
      </div>
      </>
    );
  };
  
  export default Blog;