import React, { useState } from "react"
import {AppBar, Typography, Toolbar, Box, Button, Tabs, Tab} from "@mui/material"
import { Link } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { authActions } from "../store"
import { useStyles } from  "./utils.js"


const Header = () => {
  const classes = useStyles()
  const dispath = useDispatch()
  const [value, setvalue] = useState()
  const isLoggedIn = useSelector(state => state.isLoggedIn);
  return (
    <>
     <AppBar position="sticky" sx={{background:"#605C3C"}}>
        <Toolbar>
            <Typography className={classes.font} variant="h4">BlogsApp</Typography>
            {isLoggedIn && <Box display="flex" marginLeft="auto" marginRight={"auto"}>
              <Tabs textColor="inherit" value={value} onChange={(e,val)=>setvalue(val)}>
                <Tab className={classes.font} LinkComponent={Link} to="/blogs" label="all blogs" />
                <Tab className={classes.font} LinkComponent={Link} to="/myblogs" label="my blogs" />
                <Tab className={classes.font} LinkComponent={Link} to="/blogs/add" label="Add blogs" />
              </Tabs>
            </Box>
}
            <Box display="flex" marginLeft="auto" >
                { !isLoggedIn && <> <Button 
                variant="contained" 
                sx= {{margin:1,borderRadius: 10 }} 
                color="warning"
                LinkComponent={Link} to="/login">Login
                </Button>
                <Button variant="contained"
                 sx= {{margin:1,borderRadius: 10 }} 
                color="warning"
                LinkComponent={Link} to="/signup">Signup
                </Button>  
                 </>}
                {isLoggedIn && <Button variant="contained"
                 sx= {{margin:1,borderRadius: 10 }} 
                color="warning"
                LinkComponent={Link} to="/auth"
                onClick={()=>dispath(authActions.logout())}>Logout</Button>}
            </Box>
        </Toolbar>
    </AppBar> 
    </>
  )
}

export default Header