import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import axios from "axios"
import { authActions } from '../store'
import { Avatar, Button, colors, ExtractText, StyledFormArea, StyledFormButton, StyledTitle, TextLink, CopyRight, StyledContainer } from '../components/Styles'
import {Formik,Form} from 'formik'
import woman from "../assets/woman.png"
import { TextInput } from '../components/Formlib.js'
import {FiMail,FiLock} from "react-icons/fi"
import * as Yup from "yup"
import { Audio } from 'react-loader-spinner'



const Signup = () => {
  const dispath = useDispatch()
  const navigate = useNavigate()
  const [error, setError] = useState()
   
  return (
    <StyledContainer>
    <div>
        <StyledFormArea>
            <Avatar image={woman}/>
            <StyledTitle color={colors.theme} size={30}>
                Member Signup
            </StyledTitle>
            <Formik
                initialValues = {{name:"", email:"" , password:"" ,repeatPassword:""}}
                validationSchema={
                    Yup.object({
                        email:Yup.string()
                         .email("invalid email")
                         .matches( /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/)
                         .required("required"),
                        password:Yup.string()
                         .min(6,"password is too short")
                         .max(30,"password is too long")
                         .required("required"),
                        name:Yup.string()
                         .required("required"),
                        repeatPassword:Yup.string()
                         .required("required").oneOf([Yup.ref("password")],"Passwords dont match"), 
                })}
                 onSubmit={(values,{setSubmitting})=>{
                    //console.log(values)
                    setTimeout(() => { 
                        const sendRequest = async (type="signup") => {
                          const res = await axios.post(`http://localhost:4000/api/user/${type}`, {
                            name:values.name,
                            email:values.email,
                            password:values.password,
                          })
                          .catch(err => {
                            console.log(err.response.data.msg)
                             setError(err.response.data.msg)
                             navigate("/login")
                          })
                          const data = await res.data
                          return data;
                        }   
                        //e.preventDefault();
                        //console.log(values)
                        sendRequest().then((data)=>localStorage.setItem("userId", data.user._id))
                        .then(()=> dispath(authActions.login()))
                        .then(()=> navigate("/blogs"));
                    }, 1);
                    
                 }}                
            >                
                {({isSubmitting}) => (
                    <Form>
                        {isSubmitting && ( error && alert(error) ) }
                        <TextInput 
                            name="name"
                            placeholder="Name"
                            type="text"
                            label="Enter your name:" 
                            icon={<FiLock />}/>
                        <TextInput 
                            name="email"
                            placeholder="Email"
                            type="text"
                            label="Email Address" 
                            icon={<FiMail />} />
                        <TextInput 
                            name="password"
                            placeholder="Password"
                            type="text"
                            label="Input your password" 
                            icon={<FiLock />}/>
                        <TextInput
                            name="repeatPassword"
                            placeholder="Confirm Password"
                            type="text"
                            label="Confirm your password:" 
                            icon={<FiLock />}/> 
                        <Button>
                            {!isSubmitting && (<StyledFormButton type="submit">
                                Signup
                            </StyledFormButton>)}
                            {isSubmitting && (
                                <Audio
                                height={80}
                                width={80}
                                radius={9}
                                color={colors.theme}
                                ariaLabel="loading"
                                wrapperStyle
                                wrapperClass
                              />
                            )}
                        </Button>
                    </Form>
                )}
            </Formik>
            <ExtractText>
                Already have a account? <TextLink to="/login">Login</TextLink>
            </ExtractText>
        </StyledFormArea>
        <CopyRight>
            All rights reserved &copy;2023
        </CopyRight>
    </div>
    </StyledContainer>
  )
}

export default Signup











